<template>
  <div>
    <searchnav title="个人中心"
               :show="false"></searchnav>
    <div>
      <h2>token---{{token}}</h2>
      <h2>{{info.username}}</h2>
      <h2>{{info.radio}}</h2>
      <h2>爱好---<span v-for="(item, index) in info.checkboxGroup"
              :key="index">{{item}}</span></h2>
      <h2>性别---{{info.radio}}</h2>
    </div>
    <uploading :src='src'></uploading>
    <van-button class="primary"
                @click="changenum">单击改变数字</van-button>
    <van-button class="success"
                @click="changNUM">单击改变数字</van-button>
  </div>
</template>
<script>
import {
  mapMutations
} from 'vuex'
import { NEW } from '@/store/aa'
export default {
  data () {
    return {
      token: sessionStorage.token || '',
      info: [],
      src: ''
    }
  },
  methods: {
    ...mapMutations([
      'changenum',   //this.changenum=this.$store.commit('changenum')
    ]),


    // changnum () {
    //   this.$store.commit('changenum')
    // },
    changNUM () {
      this.$store.commit(NEW)
    }
  },
  created () {
    this.axios.get('/vue/info').then(res => {
      console.log(res)
      if (res.data.type === 1) {
        this.info = res.data.data;
        if (res.data.data.path) {
          this.src = res.data.data.path.replace(/public/, 'http://localhost:4000')
          localStorage.avatar = this.src
        } else {
          this.src = localStorage.avatar
        }
      }
      if (res.data.code === '3000') {
        this.$router.push({ name: 'regiest' })
      }
    })
  },

}
</script>
<style lang="scss" scoped>
</style>